* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Epilogue, sans-serif;
}

@media (max-width: 767.99px) {
  header {
    z-index: 1000;
    width: 100%;
    background: var(--black, #141414);
    justify-content: center;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
  }

  .header-container {
    width: 375px;
  }

  nav .mainMenu li a {
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    justify-content: center;
    padding: 15px;
    font-family: Epilogue;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
    text-decoration: none;
    display: flex;
  }

  .mainMenu li a:hover {
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    box-shadow: 0 5px 40px -10px rgba(0, 0, 0, .57);
  }

  .mainMenu {
    width: 100%;
    height: 100%;
    z-index: 1000;
    justify-content: center;
    list-style: none;
    display: flex;
    position: fixed;
  }

  .logo-header-mobile {
    margin-left: 10px;
  }

  nav .mainMenu li a:hover {
    color: #595e5e;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
  }

  nav .openMenu {
    cursor: pointer;
    margin: 20px 10px 20px 20px;
    font-size: 2rem;
    display: none;
  }

  nav .mainMenu .closeMenu, nav .logo {
    cursor: pointer;
    text-transform: uppercase;
    margin: 6px;
  }

  .burger-header {
    width: 24px;
    height: 24px;
  }

  .burger-header:hover {
    transform: translateY(2px);
    box-shadow: inset 0 10px 20px 2px rgba(0, 0, 0, .25);
  }

  .svg-close {
    width: 24px;
    height: 24px;
  }

  .svg-close:hover {
    border-radius: 50%;
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
    transform: translateY(2px);
    box-shadow: inset 0 10px 20px 2px rgba(0, 0, 0, .25);
  }

  .svg-logo {
    width: 14px;
    height: 14px;
  }

  .text-logo {
    color: #fff;
    letter-spacing: -.36px;
    width: 80px;
    height: 10px;
    font-family: Epilogue;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .svg-header-mobile {
    width: 14px;
    height: 14px;
  }

  .text-logo-mobile {
    color: #fff;
    letter-spacing: -.36px;
    width: 80px;
    height: 10px;
    font-family: Epilogue;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  nav {
    color: #fff;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    display: flex;
  }

  nav .mainMenu {
    z-index: 10;
    background-color: var(--dark-green, #1e2827);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: top 1s;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
  }

  .anchor {
    color: #fff;
    letter-spacing: -.28px;
    text-transform: uppercase;
    font-family: Epilogue;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
  }

  nav ul {
    margin-top: 20px;
    margin-bottom: 250px;
    list-style: none;
  }

  nav .logo-header {
    display: block;
    position: absolute;
    top: 20px;
    left: 20px;
  }

  nav .mainMenu .closeMenu {
    display: block;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  nav .openMenu {
    display: block;
  }

  .top-menu {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .address {
    display: none;
  }

  .logo-header {
    margin: 10px;
    position: absolute;
    top: 0;
    left: 0;
  }

  .logo-header:hover {
    border-radius: 10%;
    transform: translateY(2px);
    box-shadow: inset 0 10px 20px 2px rgba(0, 0, 0, .25);
  }

  .svg-header {
    width: 14px;
    height: 14px;
  }
}

